
.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(45deg, #f06, #4e54c8);
  min-height: 100vh;
  padding: 20px;
  color: white;
}

h1 {
  color: #fff;
  font-size: 36px;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 700;
  animation: fadeIn 1s ease-in-out;
}


form {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  animation: slideIn 1s ease-in-out;
}

input {
  padding: 12px;
  font-size: 16px;
  width: 300px;
  border: 2px solid #fff;
  border-radius: 8px;
  outline: none;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: all 0.3s ease;
}

input:focus {
  border-color: #f39c12;
  background-color: rgba(255, 255, 255, 0.3);
}

button {
  padding: 12px 20px;
  font-size: 16px;
  background-color: #f39c12;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #e67e22;
  transform: translateY(-2px);
}


p {
  font-size: 18px;
  color: #e74c3c;
  margin-top: 10px;
}


.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
}

.movie {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px;
  margin: 15px;
  width: 220px;
  border-radius: 8px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
  text-align: center;
  transition: all 0.3s ease;
}

.movie:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 25px rgba(0, 0, 0, 0.4);
}

.movie img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.movie img:hover {
  transform: scale(1.1);
}

.movie h3 {
  font-size: 20px;
  color: #fff;
  font-weight: bold;
}

.movie p {
  font-size: 16px;
  color: #bdc3c7;
  margin-top: 5px;
}


@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideIn {
  0% {
    transform: translateY(-30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
